.MuiPaper-root.Dossier {
  width: 344px;
  color: #ffffff;

  .deplacer {
    position: relative;
    font-size: 3em;
    color: #ffffff;
    background-color: gray;
    border-radius: 50%;
    padding: 1%;
    opacity: 0.7;
    z-index: 2;
    margin: 2%;
  }

  .image {
    width: 100%;
    height: 194px;
  }

  .info {
    padding-bottom: 0;
  }

  .modifier {
    float: right;
    color: #ffffff;
  }
}